.header {
 position: fixed;
 width: 100%;
 height: 20vh;
 background-color: rgb(0, 0, 0, .15);
 color: white;
 display: -ms-flexbox;
 display: flex;
 -ms-flex-pack: justify;
     justify-content: space-between;
 -ms-flex-align: center;
     align-items: center;
 z-index:990;
 -webkit-transition: all .5s linear;
 -o-transition: all .5s linear;
 transition: all .5s linear;
}
.headerOpen {
 position: fixed;
 width: 100%;
 height: 15vh;
 background-color: #171717;
 color: white;
 display: -ms-flexbox;
 display: flex;
 -ms-flex-pack: justify;
     justify-content: space-between;
 -ms-flex-align: center;
     align-items: center;
 z-index:990;
 -webkit-transition: all .5s linear;
 -o-transition: all .5s linear;
 transition: all .5s linear;
}
.logo-img {
 margin-left: 10vw;
}
a {
 color: white;
}
@media(min-width: 800px) {
 .logo-img {
  height: 120px;
 }
}


